<template>
<div class="container">
  <div class="card">
    <b-navbar :type="type_color">
      <template slot="brand">
        <b-navbar-item tag="div">
          <div class="title is-4 has-text-white">
            Section Transport - {{nameSection}}
          </div>
        </b-navbar-item>
      </template>
      <template slot="end">
        <b-navbar-item tag="div">
        </b-navbar-item>
        <b-navbar-item tag="div">
          <b-button v-if="formComponent"
                    :class="type_color"
                    size="is-medium"
                    icon-left="plus-circle"
                    @click="newTravel = true">
            Ajouter un voyage
          </b-button>

          <b-modal :active.sync="newTravel"
                   :can-cancel="['escape', 'x']"
                   full-screen
                   scroll="clip"
                   trap-focus
                   has-modal-card>
            <div :is="formComponent"
                 @addElement="addElement" />

          </b-modal>
        </b-navbar-item>
        <b-navbar-item tag="div">
          <b-button :class="type_color"
                    size="is-medium"
                    icon-left="chevron-up"
                    @click="reduce_all()">
          </b-button>
        </b-navbar-item>
        <b-navbar-item tag="div">
          <b-button :class="type_color"
                    size="is-medium"
                    icon-left="chevron-down"
                    @click="expand_all()">
          </b-button>
        </b-navbar-item>
      </template>
    </b-navbar>

    <div class="card-content has-background-white-bis">

      <transition-group class="columns is-multiline is-centered"
                        enter-active-class="animated fadeInLeft fast"
                        leave-active-class="animated fadeOutRight fast"
                        move-class="list-elements-move"
                        tag="div">
        <div v-for="(element, index) in elements"
             class="column is-10"
             :key="element.id">
          <b-collapse class="card"
                      aria-id="contentId"
                      :open.sync="panels[index]">
            <div slot="trigger"
                 slot-scope="props"
                 class="card-header has-background-dark"
                 role="button"
                 aria-controls="contentId">

              <div class="card-header-title is-size-5 has-text-white form-inline">
                <b-icon :icon="iconSection" />
                <span class="form-inline-element">Voyage n{{index + 1}}</span>
                <span class="form-inline-element"> - </span>

                <div v-if="edits_names[index]===false"
                     class="form-inline-element">
                  <span v-if="element.name !== ''">{{element.name}}</span>
                  <span class="is-size-6 is-italic"
                        v-else>Donnez un nom  votre voyage</span>
                  <b-tooltip label="Appuyer pour changer le nom du voyage"
                             class="form-inline-element"
                             type="is-white"
                             position="is-right">
                    <b-button icon-left="pencil"
                              size="is-medium"
                              type="is-dark"
                              @click.native.stop="setEditName(index)" />
                  </b-tooltip>
                </div>
                <div v-else
                     class="form-inline-element"
                     style="display: inline-block;">
                  <form @submit.prevent="editName(element.id)">
                    <b-field grouped>
                      <b-input v-model="edits_names[index]"
                               @click.native.stop
                               placeholder="Nom du voyage">
                      </b-input>
                      <b-button type="is-info"
                                inverted
                                @click.native.stop="editName(element.id)">
                        Valider
                      </b-button>
                    </b-field>
                  </form>
                </div>
              </div>

              <a class="card-header-icon">
                <b-tag class='is-carbon has-text-weight-bold'>{{element.carbon | splitNumber}} kgCO2e</b-tag>
              </a>

              <a class="card-header-icon">
                <b-button icon-left="delete"
                          type="is-dark"
                          size="is-medium"
                          @click.native.stop="setDeleteModals(index)" />
              </a>
              <b-modal :active.sync="delete_modals[index]"
                       scroll="keep">
                <div class="modal-card">
                  <header class="modal-card-head">
                    <p class="modal-card-title">tes vous certain de vouloir supprimer ce voyage ?</p>
                  </header>
                  <div class="modal-card-foot has-background-light">
                    <b-button type="is-success"
                              size="is-medium"
                              class="card-footer-item"
                              @click.native.stop="deleteElement(element.id, index)"> Oui </b-button>
                    <b-button type="is-danger"
                              size="is-medium"
                              class="card-footer-item"
                              @click.native.stop="delete_modals.splice(index, 1, false)"> Non </b-button>
                  </div>
                </div>
              </b-modal>

              <a class="card-header-icon">
                <b-button type="is-dark"
                          size="is-medium"
                          :icon-left="panels[index] ? 'menu-up' : 'menu-down'" />
              </a>
            </div>

            <div class="component-content">
              <div :is="contentComponent"
                   :edit="edits[index]"
                   :element="element">
              </div>
            </div>

            <b-tag class="card-footer-item has-background-primary
                        has-text-white has-text-weight-bold"
                   size="is-small">
              <span @mouseover="setTooltipDistances(index, true)"
                    @mouseleave="setTooltipDistances(index, false)">
                {{element.distance | splitNumber }} km parcourus au total</span>
            </b-tag>

            <div v-show="tooltip_distances[index]"
                 class="columns tooltip-distance is-multiline is-vcentered
                        has-background-white-bis has-text-weight-bold is-size-7">
              <div class="column is-3 has-text-centered">
                <span class="has-text-distance">Distance totale parcourue</span>
              </div>
              <div class="column is-1 has-text-centered">
                =
              </div>
              <div class="column is-2 has-text-centered">
                <span class="has-text-carbon">Distance parcourue pendant le trajet</span>
              </div>
              <div class="column is-1 has-text-centered">
                *
              </div>
              <div class="column is-2 has-text-centered">
                <span class="has-text-airplanetrans">Nombre de trajets effectus dans l'anne</span>
              </div>
              <div class="column is-1 has-text-centered">
                *
              </div>
              <div class="column is-2 has-text-centered">
                <span class="has-text-round">Aller-retour (x2) ou aller-simple (x1) ?</span>
              </div>
              <div class="column is-3 has-text-centered">
                <span class="has-text-distance">
                  {{element.distance | splitNumber }}
                </span>
              </div>
              <div class="column is-1 has-text-centered">
                =
              </div>
              <div class="column is-2 has-text-centered">
                <span class="has-text-carbon">
                  {{element.distance / ((element.round + 1) * element.times) | splitNumber }}
                </span>
              </div>
              <div class="column is-1 has-text-centered">
                *
              </div>
              <div class="column is-2 has-text-centered">
                <span class="has-text-airplanetrans">
                  {{element.times | splitNumber }}
                </span>
              </div>
              <div class="column is-1 has-text-centered">
                *
              </div>
              <div class="column is-2 has-text-centered">
                <span class="has-text-round">
                  {{element.round + 1 | splitNumber }}
                </span>
              </div>
            </div>
          </b-collapse>
        </div>
      </transition-group>
    </div>
  </div>
</div>
</template>

<script>
import App from "@/App.vue";

import AirTransportFormComponent from "@/components/transport/forms/AirTransportForm.vue";
import CarTransportFormComponent from "@/components/transport/forms/CarTransportForm.vue";
import TrainTransportFormComponent from "@/components/transport/forms/TrainTransportForm.vue";
import LightTransportFormComponent from "@/components/transport/forms/LightTransportForm.vue";
import PublicTransportFormComponent from "@/components/transport/forms/PublicTransportForm.vue";

import AirTransportContentComponent from "@/components/transport/content/AirTransportContent.vue";
import CarTransportContentComponent from "@/components/transport/content/CarTransportContent.vue";
import TrainTransportContentComponent from "@/components/transport/content/TrainTransportContent.vue";
import LightTransportContentComponent from "@/components/transport/content/LightTransportContent.vue";
import PublicTransportContentComponent from "@/components/transport/content/PublicTransportContent.vue";

import { mapState } from "vuex";
export default {
  name: "TransportSectComponent",
  components: {
    App,

    AirTransportFormComponent,
    CarTransportFormComponent,
    TrainTransportFormComponent,
    LightTransportFormComponent,
    PublicTransportFormComponent,

    AirTransportContentComponent,
    CarTransportContentComponent,
    TrainTransportContentComponent,
    LightTransportContentComponent,
    PublicTransportContentComponent,
  },
  props: {
    nameSection: String,
    iconSection: String,
    formComponent: String,
    contentComponent: String,
    background_color: String,
    type_color: String,
    endpoint: String,
  },
  data() {
    return {
      newTravel: false,
      elements: [],
      edits: [],
      edits_names: [],
      delete_modals: [],
      tooltip_distances: [],
      new_form: false,
      panels: [],
    }
  },
  computed: mapState(['year']),
  methods: {
    setDeleteModals(index) {
      this.$set(this.delete_modals, index, true);
    },
    setTooltipDistances(index, value) {
      this.$set(this.tooltip_distances, index, value);
    },
    addElement(data, id) {
      let index = this.findElement(id);
      let method = "POST";
      let endpoint = `/api/${this.endpoint}/`;
      if (index !== false) {
        method = "PUT";
        endpoint = `/api/${this.endpoint}/${id}/`;
      }
      this.$http(endpoint, method, data).then(
        res => {
          if (method === "POST") {
            this.elements.push(res.data);
            this.edits.push(false);
            this.edits_names.push(false);
            this.delete_modals.push(false);
            this.new_form = false;

          } else {
            this.elements.splice(index, 1, res.data);
            this.edits[index] = false;
          }
        }).then(() => this.newTravel = false);
    },
    editName(id) {
      let index = this.findElement(id);
      let method = "PUT";
      let endpoint = `/api/${this.endpoint}/${id}/`;
      let data = this.elements[index];
      data.name = this.edits_names[index];
      this.$http(endpoint, method, data).then(
        res => {
          this.elements.splice(index, 1, res.data);
          this.edits_names[index] = false;
        });
    },
    setEditName(index) {
      if (!this.edits_names[index]) {
        this.$set(this.edits_names, index, this.elements[index].name);
      } else {
        this.$set(this.edits_names, index, false)
      }
    },
    deleteElement(id, index) {
      this.$set(this.delete_modals, index, false);
      setTimeout(() => {
        let endpoint = `/api/${this.endpoint}/${id}/`;
        let method = "DELETE";
        this.$http(endpoint, method);
        this.delete_modals.splice(index, 1);
        this.elements.splice(index, 1);
        this.tooltip_distances.splice(index, 1);
      }, 0)

    },
    getElements() {
      let endpoint = `/api/${this.endpoint}/?year=${this.year}`;
      this.$http(endpoint)
        .then(res => {
          this.elements = [];
          this.elements.push(...res.data.results);
          this.delete_modals = new Array(this.elements.length).fill(false)
          this.edits = new Array(this.elements.length).fill(false)
          this.edits_names = new Array(this.elements.length).fill(false)
          this.panels = new Array(this.elements.length).fill(false)
          this.tooltip_distances = new Array(this.elements.length).fill(false)
        })
    },
    findElement(id) {
      for (var i = 0; i < this.elements.length; i++) {
        if (this.elements[i].id === id) {
          return i;
        }
      }
      return false;
    },
    setEdits(id) {
      this.$set(this.edits, id, !this.edits[id])
    },
    expand_all() {
      this.panels = new Array(this.elements.length).fill(true)
    },
    reduce_all() {
      this.panels = new Array(this.elements.length).fill(false)
    },
  },
  watch: {
    year(newValue, oldValue) {
      this.getElements();
    }
  },
  created() {
    this.getElements();
  }
}
</script>

<style scoped>
.form-inline {
  font-size: 1.25rem;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.form-inline-element {
  margin-left: 1rem;
  display: flex;
  align-items: center;
}

.list-elements-move {
  transition: transform 1s;
}

.tooltip-distance {
  position: absolute;
  left: 250px;
  top: 135px;
  border: 2px solid black;
  border-radius: 20px;
  z-index: 10;
}
</style>
