<template>
<div>
  <apexcharts type="donut"
              style="text-align: left;"
              :options="options"
              :series="data"></apexcharts>

</div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
export default {
  name: "SectorChart",
  props: {
    sector: String,
    labels: Array,
    year: Number,
  },
  components: {
    apexcharts: VueApexCharts
  },
  data: function() {
    return {
      data: [],
      options: {
        chart: {
          dropShadow: {
            enabled: true,
            color: '#111',
            top: -1,
            left: 3,
            blur: 3,
            opacity: 0.2
          },
          animations: {
            enabled: true,
            dynamicAnimation: {
              enabled: false,
            },
          }
        },
        plotOptions: {
          pie: {
            donut: {
              labels: {
                show: true,
                value: {
                  show: true,
                  formatter: function(val) {
                    return val + " kgCO2e"
                  }
                },
                total: {
                  show: true,
                  formatter: function(w) {
                    let total = w.globals.seriesTotals.reduce((a, b) => {
                      return a + b
                    }, 0).toFixed(0);
                    if (total == 0) {
                      return "Aucune donne pour cette anne";
                    }
                    return total + " kgCO2e";
                  }
                }
              },
            }
          }
        },
        legend: {
          position: 'top',
          horizontalAlign: 'left',
        },
        stroke: {
          width: 0,
        },
        dataLabels: {
          dropShadow: {
            blur: 3,
            opacity: 0.8
          }
        },
        states: {
          hover: {
            enabled: false
          }
        },
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 500
            },
            legend: {
              position: 'bottom'
            }
          }
        }]
      },
    }
  },
  methods: {
    getData() {
      let endpoint = `/api/summary/user/${this.sector}/${this.year}/`;
      this.$http(endpoint)
        .then(res => {
          let data = res.data
          delete data["id"];
          delete data["user"];
          this.data = Object.values(data);
          this.options = {
            labels: this.labels
          }
        })
    }
  },
  created() {
    this.getData()
  }
}
</script>

