<template>
<div>
  <div v-if="!display">
    <p>
      Aucune donne  afficher.
      <router-link :to="{ name: 'calculateur' }">
        Accder au calculateur.
      </router-link>
    </p>
  </div>

  <apexcharts type=bar
              height="570"
              :options="options"
              :series="series" />

</div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";
export default {
  name: "BarChart",
  props: {
    sector: String,
    labels: Array,
  },
  components: {
    apexcharts: VueApexCharts
  },
  data: function() {
    return {
      series: [],
      loading: true,
      display: true,
      options: {
        chart: {
          stacked: true,
          toolbar: {
            show: true
          },
          zoom: {
            enabled: true
          }
        },
        dataLabels: {
          style: {
            fontSize: '1em',
          },
        },
        responsive: [{
          breakpoint: 480,
          options: {
            legend: {
              position: 'bottom',
              offsetX: -10,
              offsetY: 0
            }
          }
        }],
        xaxis: {
          categories: ['2019'],
          labels: {
            style: {
              fontSize: '1em',
            },
          },
          title: {
            text: 'Anne',
            style: {
              fontSize: '1em',
            },
          }
        },
        yaxis: {
          labels: {
            style: {
              fontSize: '1em',
            },
          },
          title: {
            text: 'missions [kgCO2e]',
            style: {
              fontSize: '1em',
            },
          }
        },
        plotOptions: {
          bar: {
            horizontal: false,
          },
        },
        legend: {
          position: 'right',
          offsetY: 40
        },
        fill: {
          opacity: 1
        }
      }
    }
  },
  methods: {
    getData() {
      let endpoint = `/api/summary/user/${this.sector}/columns/`;
      this.$http(endpoint)
        .then(res => {
          let response = res.data;
          if (!response["years"]) {
            this.display = false;
            return;
          }
          this.options = {
            xaxis: {
              categories: response["years"]
            }
          }
          delete response["years"];
          for (var i = 0; i < Object.keys(response).length; i++) {
            let name = Object.keys(response)[i]
            let data = response[name]
            this.series.push({
              name: this.labels[i],
              data: data
            })
          };
        });
    }
  },
  created() {
    this.getData()
  }
}
</script>

