<template>
<form @submit.prevent="validate()">
  <div class="modal-card"
       style="width: auto">
    <header class="modal-card-head">
      <p class="modal-card-title">Nouveau trajet en transport en commun</p>
      <b-button icon-left="close"
                @click="$parent.close()" />
    </header>
    <section class="modal-card-body">
      <b-field label="Nom du voyage">
        <b-input v-model="form.name"
                 size="is-medium">
        </b-input>
      </b-field>

      <div class="card"
           v-for="sect in sections">
        <div class="card-header has-text-white has-background-secondary">
          <div class="card-header-title has-text-white">
            Section {{sect}}
          </div>
          <div class="card-header-icon">
            <b-button type="is-secondary"
                      icon-left="close"
                      @click="removeSection()">
            </b-button>
          </div>
        </div>


        <div class="card-content">
          <b-field :label="'Type de transport n' + sect"
                   :type="{'is-danger': errors.has('transport')}"
                   :message="errors.has('transport') ? 'Ce champ est requis' : ''">
            <b-select v-model="form.transports[sect - 1]"
                      :placeholder="'Type de transport n' + sect"
                      size="is-medium"
                      v-validate="'required'">
              <option v-for="(item, index) in transport_items"
                      :key="index">
                {{item}}
              </option>
            </b-select>
          </b-field>

          <b-field grouped>
            <b-field :label="'Distance n' + sect">
              <b-numberinput v-model="form.distances[sect - 1]"
                             size="is-medium"
                             min="1"></b-numberinput>
            </b-field>
          </b-field>
        </div>

      </div>

      <b-field style="margin: 20px 0px;">
        <b-field>
          <b-button type="is-secondary"
                    icon-left="plus"
                    size="is-medium"
                    @click="addSection()">Ajouter une section
          </b-button>
        </b-field>
      </b-field>

      <b-field grouped>
        <b-field label="Nombre de fois dans l'anne">
          <b-numberinput v-model="form.times"
                         size="is-medium"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field>
        <b-checkbox v-model="form.round"
                    size="is-medium">
          Prendre en compte le retour ?
        </b-checkbox>
      </b-field>

      <button type="submit"
              class="button is-fullwidth is-medium is-primary"
              style="margin-bottom: 30px;"> Enregistrer </button>
    </section>
  </div>
</form>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: "PublicTransportFormComponent",
  props: {
    idElement: Number,
    dataInput: Object,
  },
  data() {
    return {
      valid: true,
      sections: 1,
      form: {
        name: '',
        transports: [''],
        distances: [1],
        times: 1,
        round: false,
      },

      transport_items: [
        "Tramway",
        "RER",
        "Mtro",
        "Autobus - agglomrations de + de 250 000 habitants",
        "Autobus - agglomrations de 150 000  250 000 habitants",
        "Autobus - agglomrations de - de 150 000 habitants"
      ],

    }
  },
  computed: mapState(['year']),
  methods: {
    validate() {
      this.$validator.validateAll().then(result => {
        if (result) {
          let data = {
            name: this.form.name,
            year: this.year,
            transports_list: this.form.transports,
            distances_list: this.form.distances,
            times: this.form.times,
            round: this.form.round,
          }
          this.$emit('addElement', data, this.idElement)
        }
      })
    },
    addSection() {
      this.sections++;
      this.form.transports.push('');
      this.form.distances.push(1);
    },
    removeSection(index) {
      this.sections--;
      this.form.transports.splice(index, 1);
      this.form.distances.splice(index, 1);
    }
  }
}
</script>

