<template>
<div class="container">
  <div class="card">
    <b-navbar type="is-dark"
              class="navbar-transport">
      <template slot="brand">
        <b-navbar-item tag="div">
          <div class="title is-4 has-text-white">
            Rsum de la section Transport
          </div>
        </b-navbar-item>
      </template>
    </b-navbar>

    <b-navbar class="navbar-transport has-background-white-bis">
      <template slot="start">
        <b-navbar-item tag="div">
          <b-button type="is-light"
                    icon-left="refresh"
                    size="is-medium"
                    @click="getElements()">
            Ractualiser
          </b-button>
        </b-navbar-item>
      </template>
      <template slot="end">
        <b-navbar-item tag="div">
          <b-button type="is-primary"
                    size="is-medium"
                    @click="orderByDistance()">
            Trier par distance
          </b-button>
        </b-navbar-item>
        <b-navbar-item tag="div">
          <b-button type="is-carbon"
                    size="is-medium"
                    @click="orderByCarbon()">
            Trier par missions
          </b-button>
        </b-navbar-item>
        <b-navbar-item tag="div">
          <b-button type="is-carbon-distance"
                    size="is-medium"
                    @click="orderByCarbonDistance()">
            Trier par missions/km
          </b-button>
        </b-navbar-item>
      </template>
    </b-navbar>

    <div class="card-content has-background-white-bis">
      <transition-group name="list-elements"
                        enter-active-class="animated fadeInLeft fast"
                        leave-active-class="animated fadeOutRight fast"
                        class="columns is-multiline is-centered"
                        tag="div">
        <div v-for="(element, index) in elements"
             class="column is-10"
             :key="element.id">
          <b-collapse class="card"
                      aria-id="contentId">
            <div slot="trigger"
                 slot-scope="props"
                 class="card-header"
                 :class="element.color_class"
                 role="button"
                 aria-controls="contentId">

              <div class="card-header-title is-size-6 has-text-white form-inline">
                <b-icon :icon="element.icon" />
                <span class="form-inline-element">Voyage n{{index + 1}}</span>
                <span v-if="element.name !== ''"
                      class="form-inline-element"> - </span>

                <div v-if="edits_names[index]===false"
                     class="form-inline-element">
                  <span v-if="element.name !== ''">{{element.name}}</span>
                </div>
              </div>

              <a class="card-header-icon">
                <b-tag class="is-primary has-text-weight-bold"
                       size="is-small">
                  {{element.distance | splitNumber }} km
                </b-tag>
              </a>

              <a class="card-header-icon">
                <b-tag class='is-carbon has-text-weight-bold'
                       size="is-small">{{element.carbon | splitNumber}} kgCO2e</b-tag>
              </a>

              <a class="card-header-icon">
                <b-tag class='is-carbon-distance has-text-weight-bold'
                       size="is-small">{{(element.carbon * 1000 / element.distance).toFixed(0) | splitNumber}} gCO2e/km</b-tag>
              </a>
            </div>
          </b-collapse>
        </div>
      </transition-group>
    </div>
  </div>
</div>
</template>

<script>
import { mapState } from "vuex";
export default {
  name: "TransportSectSummaryComponent",
  props: {
    nameSection: String,
    iconSection: String,
    formComponent: String,
    contentComponent: String,
    background_color: String,
    type_color: String,
    endpoint: String,
  },
  data() {
    return {
      elements: [],
    }
  },
  computed: mapState(['year']),
  methods: {
    orderByCarbon() {
      this.elements.sort((a, b) => b.carbon - a.carbon)
    },
    orderByDistance() {
      this.elements.sort((a, b) => b.distance - a.distance)
    },
    orderByCarbonDistance() {
      this.elements.sort((a, b) => b.carbon / b.distance - a.carbon / a.distance)
    },
    getElements() {
      let endpoint = `/api/${this.endpoint}/${this.year}/`;
      this.$http(endpoint)
        .then(res => {
          this.elements = [];
          this.extractObjects(res.data.results, 'airplanetrans', 'airplane');
          this.extractObjects(res.data.results, 'cartrans', 'car');
          this.extractObjects(res.data.results, 'traintrans', 'train');
          this.extractObjects(res.data.results, 'publictrans', 'subway');
          this.extractObjects(res.data.results, 'lighttrans', 'bike');
          this.delete_modals = new Array(this.elements.length).fill(false)
          this.edits = new Array(this.elements.length).fill(false)
          this.edits_names = new Array(this.elements.length).fill(false)
        })
    },
    extractObjects(data, name, icon) {
      let objects = data[name]
      objects.map(obj => {
        obj['color_type'] = 'is-' + name
        obj['color_class'] = 'has-background-' + name
        obj['icon'] = icon
      });
      this.elements.push(...objects);
    },
  },
  watch: {
    year(newValue, oldValue) {
      this.getElements();
    }
  },
  created() {
    this.getElements();
  }
}
</script>

<style scoped>
.form-inline {
  font-size: 1.25rem;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}

.form-inline-element {
  margin-left: 1rem;
  display: flex;
  align-items: center;
}

.list-elements-move {
  transition: transform 1s;
}
</style>
