<template>
<form @submit.prevent="validate()">
  <div class="modal-card"
       style="width: auto">
    <header class="modal-card-head">
      <p class="modal-card-title">Nouveau trajet en voiture</p>
      <b-button icon-left="close"
                @click="$parent.close()" />
    </header>
    <section class="modal-card-body">
      <b-field label="Nom du voyage">
        <b-input v-model="form.name"
                 size="is-medium">
        </b-input>
      </b-field>


      <b-field label="Type de moteur/carburant"
               :type="{'is-danger': errors.has('engine')}"
               :message="errors.has('engine') ? 'Ce champ est requis' : ''">
        <b-select v-model="form.engine"
                  size="is-medium"
                  placeholder="Type de moteur"
                  name="engine"
                  v-validate="'required'">
          <option v-for="(item, index) in engine_items"
                  :key="index">
            {{item}}
          </option>
        </b-select>
      </b-field>

      <b-field grouped>
        <b-field label="Distance">
          <b-numberinput v-model="form.distance"
                         size="is-medium"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field grouped>
        <b-field label="Nombre de fois dans l'anne">
          <b-numberinput v-model="form.times"
                         size="is-medium"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field grouped>
        <b-field label="Nombre de passagers dans la voiture">
          <b-numberinput v-model="form.nb_passengers"
                         size="is-medium"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field>
        <b-checkbox v-model="form.round">
          Prendre en compte le retour ?
        </b-checkbox>
      </b-field>

      <button type="submit"
              class="button is-fullwidth  is-medium is-primary"> Enregistrer </button>
    </section>
  </div>
</form>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: "CarTransportFormComponent",
  props: {
    idElement: Number,
    dataInput: Object,
  },
  data() {
    return {
      valid: true,
      form: {
        name: '',
        engine: '',
        distance: 1,
        times: 1,
        nb_passengers: 1,
        round: false,
      },

      engine_items: [
        "Voiture Essence",
        "Voiture Diesel",
        "Voiture GPL",
        "Voiture lectrique",
        "Voiture Hybride",
        "Moto"
      ],

    }
  },
  computed: mapState(['year']),
  methods: {
    validate() {
      this.$validator.validateAll().then(result => {
        if (result) {
          let data = {
            name: this.form.name,
            year: this.year,
            engine: this.form.engine,
            distance: this.form.distance,
            times: this.form.times,
            nb_passengers: this.form.nb_passengers,
            round: this.form.round,
          }
          this.$emit('addElement', data, this.idElement)
        }
      })
    }
  }
}
</script>

