import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import apiService from '@/common/api.service.js'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    year: 2019,
    token: localStorage.getItem('user-token') || '',
    status: '',
  },
  mutations: {
    INCREASE_YEAR(state, amount=1){
      state.year += amount;
    },
    DECREASE_YEAR(state, amount=-1){
      state.year += amount;
    },
    AUTH_REQUEST(state) {
      state.status = 'loading'
    },
    AUTH_SUCCESS(state, token) {
      state.status = 'success'
      state.token = token
    },
    AUTH_ERROR(state) {
      state.status = 'error'
    },
    AUTH_LOGOUT(state) {
      state.token = '';
    }
  },
  actions: {
    updateYear(context, amount) {
        if (amount >= 0) {
            context.commit('INCREASE_YEAR', amount)
        } else {
            context.commit('DECREASE_YEAR', amount)
        }
    },
    authRequest(context, user) {
      return new Promise((resolve, reject) => {
        let endpoint = '/api/token/';
        apiService(endpoint, 'POST', user).then(res => {
          const token = res.data.access;
          localStorage.setItem('user-token', token);
          axios.defaults.headers.common['Authorization'] = 'Bearer ' + token;
          context.commit('AUTH_SUCCESS', token);
          resolve(res);
        }).catch(err => {
          context.commit('AUTH_ERROR');
          localStorage.removeItem('user-token');
          reject(err);
        })
      })
    },
    authLogout(context) {
      return new Promise((resolve, reject) => {
        context.commit('AUTH_LOGOUT');
        localStorage.removeItem('user-token');
        resolve()
      })
    }
  },

  getters: {
    isAuthenticated: state => !!state.token,
    authStatus: station => state.status,
  }
})
