<template>
<div class="card"
     style="width: 30%; margin: auto; margin-top: 130px;">
  <header class="card-header">
    <p class="card-header-title is-centered has-background-primary has-text-white">
      Crer un compte
    </p>
  </header>
  <div class="content">
    <section class="card-content">
      <form @submit.prevent="validate">
        <b-field label="Laboratoire d'affiliation">
          <b-checkbox v-model="nolab">
            Aucune affililation
          </b-checkbox>
        </b-field>
        <b-field>
          <b-autocomplete v-model="lab_input"
                          :disabled="nolab"
                          name="laboratory"
                          placeholder="Cherchez un laboratoire..."
                          :data="lab_items"
                          field="short_name"
                          keep-first>
            <template slot="empty">
              Aucun rsultat
            </template>
            <template slot-scope="props">
              <div class="is-pulled-left has-text-weight-bold">
                {{ props.option.short_name }}
              </div>
              <br>
              <small>
                {{ props.option.full_name }}
              </small>
            </template>
          </b-autocomplete>
        </b-field>

        <b-field label="Adresse e-mail">
          <b-input v-model="form.email"
                   type="email"
                   placeholder="Example@lsce.ipsl.fr">
          </b-input>
        </b-field>

        <b-field label="Mot de passe">
          <b-input v-model="form.password1"
                   type="password"
                   placeholder="Mot de passe"></b-input>
        </b-field>

        <b-field label="Confirmation du mot de passe">
          <b-input v-model="form.password2"
                   type="password"
                   @keydown.enter="validate"
                   placeholder="Retapez votre mot de passe"></b-input>
        </b-field>

        <button class="button is-primary is-fullwidth"
                type="submit">S'enregistrer</button>
      </form>
    </section>
    <section class="card-footer">
      <div class="card-footer-item has-text-centered">
        Vous avez dj compte ?
        <router-link :to="{ name: 'login' }"
                     style="padding-left: 0.3em;">
          Connectez-vous ici.
        </router-link>
      </div>
    </section>
  </div>

</div>
</template>

<script>
import laboratories from "@/../data/laboratories.json"
export default {
  name: "Register",
  data() {
    return {
      laboratories: laboratories,
      nolab: false,
      form: {
        email: null,
        lab: null,
        password1: null,
        password2: null,
      },

      error: false,

      lab_items: [],
      lab_input: '',
    }
  },
  methods: {
    validate() {
      let endpoint = '/api/auth/registration/';
      this.form.email = this.form.email.toLowerCase();
      if (this.nolab) {
        this.form.lab = null;
      } else {
        this.form.lab = this.lab_input;
      }
      let email = this.form.email;
      let password = this.form.password1;
      this.$http(endpoint, 'POST', this.form)
        .then(() => {
          return this.$store.dispatch('authRequest', { email, password })
        }).then(() => {
          this.$router.push('/');
        }).catch(err => {
          this.error = true;
        })
    },
    filterLaboratories(v) {
      this.lab_items = this.laboratories.filter(object => {
        return (object.full_name + object.short_name || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
      });
    },
  },
  watch: {
    lab_input: function(input) {
      input && this.filterLaboratories(input);
    }
  }
}
</script>

