<template>
<form @submit.prevent="validate()">
  <div class="modal-card"
       style="width: auto">
    <header class="modal-card-head">
      <p class="modal-card-title">Nouveau trajet en transport lger</p>
      <b-button icon-left="close"
                @click="$parent.close()" />
    </header>
    <section class="modal-card-body">
      <b-field label="Nom du voyage">
        <b-input v-model="form.name"
                 size="is-medium">
        </b-input>
      </b-field>


      <b-field label="Type de transport"
               :type="{'is-danger': errors.has('transport')}"
               :message="errors.has('transport') ? 'Ce champ est requis' : ''">
        <b-select v-model="form.transport"
                  size="is-medium"
                  placeholder="Type de transport"
                  name="transport"
                  v-validate="'required'">
          <option v-for="(item, index) in transport_items"
                  :key="index">
            {{item}}
          </option>
        </b-select>
      </b-field>

      <b-field grouped>
        <b-field label="Distance">
          <b-numberinput v-model="form.distance"
                         size="is-medium"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field grouped>
        <b-field label="Nombre de fois dans l'anne">
          <b-numberinput v-model="form.times"
                         size="is-medium"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field>
        <b-checkbox v-model="form.round"
                    size="is-medium">
          Prendre en compte le retour ?
        </b-checkbox>
      </b-field>

      <button type="submit"
              class="button is-fullwidth is-medium is-primary"> Enregistrer </button>
    </section>
  </div>
</form>
</template>

<script>
import { mapState } from 'vuex';
export default {
  name: "LightTransportFormComponent",
  props: {
    idElement: Number,
    dataInput: Object,
  },
  data() {
    return {
      valid: true,
      form: {
        name: '',
        transport: '',
        distance: 1,
        times: 1,
        round: false,
      },

      transport_items: [
        "Vlo",
        "Vlo lectrique",
        "Trottinette lectrique",
      ],

    }
  },
  computed: mapState(['year']),
  methods: {
    validate() {
      this.$validator.validateAll().then(result => {
        if (result) {
          let data = {
            name: this.form.name,
            year: this.year,
            transport: this.form.transport,
            distance: this.form.distance,
            times: this.form.times,
            round: this.form.round,
          }
          this.$emit('addElement', data, this.idElement)
        }
      })
    }
  }
}
</script>

