<script>
import SectorChart from "@/components/dataviz/SectorChart.vue"
import BarChart from "@/components/dataviz/BarChart.vue"
import {
  apiService
} from "../common/api.service.js"
export default {
  name: "bilan",
  components: {
    SectorChart,
    BarChart,
  },
  data: function() {
    return {
      toggle_sector_id: 1,
      nb_lab_users: undefined,
      year1: 2019,
      year2: 2020,
      sectors: {
        total: ["Transport"],
        transport: ["Avion", "Voiture/Moto", "Train",
          'Transport public', 'Transport lger'
        ]
      },
      loading: true,
      elevation_sect: 9,
    }
  },
  computed: {
    currentSector: function() {
      return Object.keys(this.sectors)[this.toggle_sector_id];
    }
  },
  methods: {
    getDataLab() {
      let sector = Object.keys(this.sectors)[this.toggle_sector_id];
      let endpointSum = `/api/summary/lab/${sector}/sum/`;
      let endpointMean = `/api/summary/lab/${sector}/mean/`;
      apiService(endpointSum)
        .then(data => {
          delete data["nb_lab_users"];
          this.dataLabSum_wait = Object.values(data);
        });
      apiService(endpointMean)
        .then(data => {
          this.nb_lab_users = data["nb_lab_users"];
          delete data["nb_lab_users"];
          this.dataLabMean_wait = Object.values(data);
        });
    },
    getNbUsers() {
      let endpoint = `/api/summary/lab/nbusers/`;
      this.$http(endpoint)
        .then(res => {
          this.nb_lab_users = res.data["nb_lab_users"];
        });
    },
  },
  created() {
    this.getNbUsers();
  }
}
</script>

