<template>
<form @submit.prevent="validate()">
  <div class="modal-card"
       style="width: auto">
    <header class="modal-card-head">
      <p class="modal-card-title">Nouveau voyage en avion</p>
      <b-button icon-left="close"
                @click="$parent.close()" />
    </header>
    <section class="modal-card-body">
      <b-field label="Nom du voyage">
        <b-input v-model="form.name"
                 size="is-medium">
        </b-input>
      </b-field>

      <b-field>
        <b-field label="Aroport de dpart"
                 :type="{'is-danger': errors.has('airport_departure')}"
                 :message="errors.has('airport_departure') ? 'Ce champ est requis' : ''">
          <b-autocomplete v-model="input_airports[0]"
                          name="airport_departure"
                          size="is-medium"
                          placeholder="Cherchez un aroport..."
                          :data="items[0]"
                          icon="airplane-takeoff"
                          field="name"
                          keep-first
                          @select="refreshDistances($event, 0)"
                          v-validate="'required'">
            <template slot="empty">
              <span v-if="input_airports[0].length > 2">Aucun rsultat</span>
              <span v-else>Tapez au moins 3 caractres</span>
            </template>
            <template slot-scope="props">
              <div class="is-pulled-left has-text-weight-bold">
                {{ props.option.name }}
              </div>
              <div class="is-pulled-right has-text-weight-bold">
                {{ props.option.IATA }}
              </div>
              <br>
              <small>
                {{ props.option.city }}, {{ props.option.country }}
              </small>
            </template>
          </b-autocomplete>
        </b-field>
      </b-field>

      <b-field v-for="c in connexions">
        <b-field :label="'Aroport de transit n' + c"
                 :type="{'is-danger': errors.has('airport_connexion')}"
                 :message="errors.has('airport_connexion') ? 'Ce champ est requis' : ''">
          <b-field grouped>
            <b-autocomplete v-model="input_airports[c + 1]"
                            name="airport_connexion"
                            size="is-medium"
                            :data="items[c + 1]"
                            placeholder="Cherchez un aroport..."
                            icon="transit-connection"
                            field="name"
                            @select="refreshDistances($event, c + 1)"
                            v-validate="'required'">
              <template slot="empty">
                <span v-if="input_airports[c + 1].length > 2">Aucun rsultat</span>
                <span v-else>Tapez au moins 3 caractres</span>
              </template>
              <template slot-scope="props">
                <div class="is-pulled-left has-text-weight-bold">
                  {{ props.option.name }}
                </div>
                <div class="is-pulled-right has-text-weight-bold">
                  {{ props.option.IATA }}
                </div>
                <br>
                <small>
                  {{ props.option.city }}, {{ props.option.country }}
                </small>
              </template>
            </b-autocomplete>

            <b-button type="is-danger"
                      icon-left="close"
                      @click="removeConnexion()">
            </b-button>
          </b-field>
        </b-field>
      </b-field>

      <b-field grouped>
        <b-field>
          <b-button type="is-secondary"
                    icon-left="plus"
                    size="is-medium"
                    @click="addConnexion()">Ajouter une connexion</b-button>
        </b-field>
      </b-field>

      <b-field>
        <b-field label="Aroport de d'arrive"
                 :type="{'is-danger': errors.has('airport_arrival')}"
                 :message="errors.has('airport_arrival') ? 'Ce champ est requis' : ''">
          <b-autocomplete v-model="input_airports[1]"
                          size="is-medium"
                          :data="items[1]"
                          icon="airplane-landing"
                          placeholder="Cherchez un aroport..."
                          name="airport_arrival"
                          field="name"
                          @select="refreshDistances($event, 1)"
                          v-validate="'required'">
            <template slot="empty">
              <span v-if="input_airports[1].length > 2">Aucun rsultat</span>
              <span v-else>Tapez au moins 3 caractres</span>
            </template>
            <template slot-scope="props">
              <div class="is-pulled-left has-text-weight-bold">
                {{ props.option.name }}
              </div>
              <div class="is-pulled-right has-text-weight-bold">
                {{ props.option.IATA }}
              </div>
              <br>
              <small>
                {{ props.option.city }}, {{ props.option.country }}
              </small>
            </template>
          </b-autocomplete>
        </b-field>
      </b-field>

      <b-field position="is-centered">
        <b-field label="Nombre de fois dans l'anne">
          <b-numberinput v-model="form.times"
                         size="is-medium"
                         min="1">
          </b-numberinput>
        </b-field>
      </b-field>

      <b-field>
        <b-checkbox v-model="form.round"
                    size="is-medium">
          Prendre en compte le retour ?
        </b-checkbox>
      </b-field>

      <button type="submit"
              class="button is-fullwidth is-medium is-primary"> Enregistrer </button>
    </section>
  </div>
</form>
</template>

<script>
import { mapState } from 'vuex';
import airports from "@/../data/airports.json"
export default {
  name: "AirTransportFormComponent",
  props: {
    idElement: Number,
    dataInput: Object,
  },
  data() {
    return {
      airports: airports,
      valid: true,
      form: {
        name: null,
        airports: [null, null],
        round: false,
        times: 1,
      },
      name: null,
      connexions: 0,
      distances: [null],
      loadings: [false, false],

      input_airports: ['', ''],
      items: [
        [],
        []
      ],
    }
  },

  computed: {
    ...mapState(['year']),
  },

  methods: {
    validate() {
      this.$validator.validateAll().then(result => {
        if (result) {
          let airports_text = this.form.airports.map(x => {
            return x.city + ', ' + x.country;
          })
          let data = {
            name: this.form.name,
            year: this.year,
            departure: this.autoCompleteText(this.form.airports[0]),
            arrival: this.autoCompleteText(this.form.airports[1]),
            connexions_list: this.connexions > 0 ? airports_text.slice(2) : null,
            distances_list: this.distances,
            round: this.form.round,
            times: this.form.times,
          }
          this.$emit('addElement', data, this.idElement);
        } else {
          console.log('validation error');
        }
      })
    },
    refreshDistances(option, id) {
      this.$set(this.form.airports, id, option);
      var lats = new Array(this.form.airports.length).fill(0);
      var lons = new Array(this.form.airports.length).fill(0);
      for (var j = 0; j < this.form.airports.length; j++) {
        if (this.form.airports[j]) {
          lats[j] = this.form.airports[j].lat;
          lons[j] = this.form.airports[j].lon;
        }
      }
      if (this.form.airports.length > 2) {
        lats.push(lats.splice(1, 1));
        lons.push(lons.splice(1, 1));
      }
      for (var i = 0; i < this.form.airports.length - 1; i++) {
        this.$set(this.distances, i, this.computeDistance(lats[i], lats[i + 1], lons[i], lons[i + 1]));
      }
    },
    computeDistance(lat1, lat2, lon1, lon2) {
      var R = 6371; // km
      var x1 = lat2 - lat1;
      var dLat = this.toRad(x1);
      var x2 = lon2 - lon1;
      var dLon = this.toRad(x2);
      var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) *
        Math.sin(dLon / 2) * Math.sin(dLon / 2);
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
      var d = Math.round(R * c);

      return d;
    },
    toRad(x) {
      return x * Math.PI / 180;
    },
    autoCompleteText(item) {
      if (item) {
        return item.IATA + ' | ' + item.name +
          '  ' + item.city + ', ' + item.country;
      } else {
        return null;
      }
    },
    addConnexion() {
      this.connexions++;
      this.input_airports.push('');
      this.form.airports.push(null);
      this.loadings.push(null);
      this.items.push([]);
    },
    removeConnexion(index) {
      this.connexions--;
      this.input_airports.splice(index, 1);
      this.form.airports.splice(index, 1);
      this.loadings.splice(index, 1);
      this.items.splice(index, 1);
    },
    filterAirports(v, id) {
      this.items[id] = this.airports.filter(object => {
        return (this.autoCompleteText(object) || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
      });
    },
  },
  watch: {
    input_airports: {
      handler(inputs) {
        for (var i = 0; i < inputs.length; i++) {
          if (inputs[i].length <= 2) { this.items[i] = []; }
          inputs[i] && inputs[i].length > 2 && this.filterAirports(inputs[i], i)
        }
      },
      deep: true
    }
  }
}
</script>

