<template>
<form @submit.prevent="validate()">
  <div class="modal-card"
       style="width: auto">
    <header class="modal-card-head">
      <p class="modal-card-title">Nouveau voyage en train</p>
      <b-button icon-left="close"
                @click="$parent.close()" />
    </header>
    <section class="modal-card-body">
      <b-field label="Nom du voyage">
        <b-input v-model="form.name"
                 size="is-medium">
        </b-input>
      </b-field>

      <b-field>
        <b-field label="Gare de dpart"
                 :type="{'is-danger': errors.has('station_departure')}"
                 :message="errors.has('station_departure') ? 'Ce champ est requis' : ''">
          <b-autocomplete v-model="input_stations[0]"
                          size="is-medium"
                          placeholder="Cherchez une gare..."
                          :data="items[0]"
                          icon="magnify"
                          field="n"
                          name="station_departure"
                          v-validate="'required'"
                          @select="refreshDistances($event, 0)">
            <template slot="empty">
              <span v-if="input_stations[0].length > 3">Aucun rsultat</span>
              <span v-else>Tapez au moins 4 caractres</span>
            </template>
            <template slot-scope="props">
              <div class="is-pulled-left has-text-weight-bold">
                {{ props.option.n }}
              </div>
              <br>
              <small>
                {{ iso_names[props.option.c] }}
              </small>
            </template>
          </b-autocomplete>
        </b-field>

      </b-field>

      <b-field grouped
               v-for="c in connexions">
        <b-field :label="'Gare de transit n' + (c + 1)"
                 :type="{'is-danger': errors.has('station_connexion')}"
                 :message="errors.has('station_connexion') ? 'Ce champ est requis' : ''">
          <b-field grouped
                   :type="{'is-danger': errors.has('station_connexion')}">
            <b-autocomplete v-model="input_stations[c + 1]"
                            size="is-medium"
                            :data="items[c + 1]"
                            placeholder="Cherchez une gare..."
                            icon="magnify"
                            field="n"
                            name="station_connexion"
                            v-validate="'required'"
                            @select="refreshDistances($event, c + 1)">
              <template slot="empty">
                <span v-if="input_stations[c + 1].length > 3">Aucun rsultat</span>
                <span v-else>Tapez au moins 4 caractres</span>
              </template>
              <template slot-scope="props">
                <div class="is-pulled-left has-text-weight-bold">
                  {{ props.option.n }}
                </div>
                <br>
                <small>
                  {{ iso_names[props.option.c] }}
                </small>
              </template>
            </b-autocomplete>

            <b-button type="is-danger"
                      icon-left="close"
                      @click="removeConnexion()">
            </b-button>
          </b-field>
        </b-field>
      </b-field>

      <b-field>
        <b-field>
          <b-button type="is-secondary"
                    icon-left="plus"
                    size="is-medium"
                    @click="addConnexion()">Ajouter une gare de transit</b-button>
        </b-field>
      </b-field>

      <b-field>
        <b-field label="Gare d'arrive"
                 :type="{'is-danger': errors.has('station_arrival')}"
                 :message="errors.has('station_arrival') ? 'Ce champ est requis' : ''">
          <b-autocomplete v-model="input_stations[1]"
                          size="is-medium"
                          placeholder="Cherchez une gare..."
                          :data="items[1]"
                          icon="magnify"
                          field="n"
                          name="station_arrival"
                          v-validate="'required'"
                          @select="refreshDistances($event, 1)">
            <template slot="empty">
              <span v-if="input_stations[1].length > 3">Aucun rsultat</span>
              <span v-else>Tapez au moins 4 caractres</span>
            </template>
            <template slot-scope="props">
              <div class="is-pulled-left has-text-weight-bold">
                {{ props.option.n }}
              </div>
              <br>
              <small>
                {{ iso_names[props.option.c] }}
              </small>
            </template>
          </b-autocomplete>
        </b-field>
      </b-field>

      <b-field>
        <b-field label="Nombre de fois dans l'anne">
          <b-numberinput size="is-medium"
                         v-model="form.times"
                         min="1"></b-numberinput>
        </b-field>
      </b-field>

      <b-field>
        <b-checkbox size="is-medium"
                    v-model="form.round">
          Prendre en compte le retour ?
        </b-checkbox>
      </b-field>

      <button type="submit"
              class="button is-fullwidth is-medium is-primary"> Enregistrer </button>
    </section>

  </div>
</form>
</template>

<script>
import stations from "@/../data/stations.json"
import iso_names from "@/../data/isonames.json"
import { mapState } from 'vuex'
export default {
  name: "TrainTransportFormComponent",
  props: {
    idElement: Number,
    dataInput: Object,
  },
  data() {
    return {
      stations: stations,
      iso_names: iso_names,
      form: {
        name: '',
        train_type: '',
        region: '',
        times: 1,
        stations: [null, null],
        round: false,
        exact_distance: 0,
      },
      distances: [null],
      connexions: 0,
      loadings: [false, false],
      input_stations: ['', ''],
      items: [
        [],
        [],
      ],
    }
  },
  computed: {
    ...mapState(['year']),
    categories: function() {
      var items = new Array(this.form.stations.length - 1).fill(null);
      for (var i = 0; i < this.form.stations.length - 1; i++) {
        if (this.form.stations[i] === null || this.form.stations[i + 1] == null) {
          items[i] = null;
        } else {
          let country1 = this.form.stations[i].c
          let country2 = this.form.stations[i + 1].c
          let isFullFrance = (country1 === 'FR' || country1 === 'CH') &&
            (country2 === 'FR' || country2 === 'CH')
          let isPartFrance = (country1 === 'FR' || country1 === 'CH') ||
            (country2 === 'FR' || country2 === 'CH')
          if (isFullFrance) {
            if (this.distances[i] > 200) {
              items[i] = 'TGV (trajet > 200 km)'
            } else {
              items[i] = 'Train trajet < 200 km (TER, Intercits, Transiliens)'
            }
          } else if (isPartFrance) {
            items[i] = 'Train trajet mixte en France et international'
          } else {
            items[i] = 'Train trajet exclusivement international'
          }
        }
      }
      return items;
    },
    message_distance: function() {
      if (!this.form.stations.includes(null)) {
        var total_distance = this.distances.reduce((a, b) => a + b, 0);
        return 'Distance totale : ' + total_distance + ' km'
      } else {
        return 'Renseignez toutes les stations pour obtenir la distance totale';
      }
    },
  },
  methods: {
    validate() {
      this.$validator.validateAll().then(result => {
        if (result) {
          let stations_text = this.form.stations.map(x => {
            return this.autoCompleteText(x);
          })
          let data = {
            name: this.form.name,
            year: this.year,
            departure: this.autoCompleteText(this.form.stations[0]),
            arrival: this.autoCompleteText(this.form.stations[1]),
            connexions_list: this.connexions > 0 ? stations_text.slice(2) : null,
            categories_list: this.categories,
            times: this.form.times,
            distances_list: this.distances,
            round: this.form.round,
          }
          this.$emit('addElement', data, this.idElement)
        }
      })
    },
    refreshDistances(option, id) {
      this.$set(this.form.stations, id, option);
      var lats = new Array(this.form.stations.length).fill(0);
      var lons = new Array(this.form.stations.length).fill(0);
      for (var j = 0; j < this.form.stations.length; j++) {
        if (this.form.stations[j]) {
          lats[j] = this.form.stations[j].la;
          lons[j] = this.form.stations[j].lo;
        }
      }
      if (this.form.stations.length > 2) {
        lats.push(lats.splice(1, 1));
        lons.push(lons.splice(1, 1));
      }
      for (var i = 0; i < this.form.stations.length - 1; i++) {
        this.$set(this.distances, i, this.computeDistance(lats[i], lats[i + 1], lons[i], lons[i + 1]));
      }
    },
    computeDistance(lat1, lat2, lon1, lon2) {
      var R = 6371; // km
      var x1 = lat2 - lat1;
      var dLat = this.toRad(x1);
      var x2 = lon2 - lon1;
      var dLon = this.toRad(x2);
      var a = Math.sin(dLat / 2) * Math.sin(dLat / 2) +
        Math.cos(this.toRad(lat1)) * Math.cos(this.toRad(lat2)) *
        Math.sin(dLon / 2) * Math.sin(dLon / 2);
      var c = 2 * Math.atan2(Math.sqrt(a), Math.sqrt(1 - a));
      var d = Math.round(R * c);

      return d;
    },
    autoCompleteText(item) {
      if (item) {
        return item.n + ', ' + this.iso_names[item.c];
      } else {
        return null;
      }
    },
    toRad(x) {
      return x * Math.PI / 180;
    },
    addConnexion() {
      this.connexions++;
      this.form.stations.push(null);
      this.input_stations.push(null);
      this.loadings.push(null);
      this.items.push([]);
    },
    removeConnexion(index) {
      this.connexions--;
      this.form.stations.splice(index, 1);
      this.input_stations.splice(index, 1);
      this.loadings.splice(index, 1);
      this.items.splice(index, 1);
    },
    filterStations(v, id) {
      this.items[id] = this.stations.filter(object => {
        return (this.autoCompleteText(object) || '').toLowerCase().indexOf((v || '').toLowerCase()) > -1
      });
    },
  },
  watch: {
    input_stations: {
      handler(inputs) {
        for (var i = 0; i < inputs.length; i++) {
          if (inputs[i].length <= 2) { this.items[i] = []; }
          inputs[i] && inputs[i].length > 3 && this.filterStations(inputs[i], i)
        }
      },
      deep: true
    }
  }
}
</script>

<style>
</style>
